<template>
  <div>
    <button @click="btnClick"></button>
  </div>
</template>

<script>
export default {
  data() {
    return {
      websock: null
    };
  },
  destroyed() {
    //ws
    if (this.websock) {
      this.websock.close(); // websocket
    }
  },
  methods: {
    //weosocket
    initWebSocket() {
      if (typeof WebSocket === "undefined") {
        alert("WebSocket");
        return false;
      }
      const wsuri = "ws://localhost:2333"; // websocket
      this.websock = new WebSocket(wsuri);
      this.websock.onopen = this.websocketonopen;
      this.websock.onmessage = this.websocketonmessage;
      this.websock.onerror = this.websocketonerror;
      this.websock.onclose = this.websocketclose;
    },
    //
    websocketonopen() {
      console.log("WebSocket");
    },
    //
    websocketonmessage(e) {
      let dataJson = JSON.parse(e.data);
      console.log(dataJson);
      // 
    },
    //
    websocketonerror(e) {
      console.log(``, e);
      this.initWebSocket(); // 
    },
    //
    websocketclose(e) {
      console.log("", e);
    },
    // 
    btnClick() {
      // ws
      if (this.websock) {
        this.websock.close(); // websocket
      }
      this.initWebSocket(); // weosocket
      // 
      setTimeout(() => {
        //OPEN
        if (this.websock.readyState === 1) {
          // 
          let obj = {
            client_id: "2",
            user_id: "3",
            to_user_id: "4",
            content: "heh",
            status: "1"
          };
          //
          this.websock.send(JSON.stringify(obj));
          console.log(obj)
        }
      }, 500),
      this.axios.post('im.test/api/websocket/chat',{
            client_id: "2",
            user_id: "3",
            to_user_id: "4",
            content: "heh",
            status: "1"
      }).then((res)=>{
          console.log(res)
      })
    }
  }
};
</script>

<style scoped lang="less">
</style>



