<template>
  <div>
    <Navbar :title="title" :lflag="lflag" @go="goMymess"></Navbar>
    <ul class="mar_L32 mar_T12">
        <!--  -->
      <li @click="addFriend">
        <img src="../../../../static/imgs/addfriend.png" alt />
        <p></p>
      </li>
      <!--  -->
      <li>
        <img src="../../../../static/imgs/newfriend.png" alt />
        <p @click="jump"></p>
      </li>
    </ul>
    <div class="friendlist" v-if="islist">
        <!-- x? mounted-->
        <p></p>
        <!--  -->
        <div class="list mar_L32" v-for="(item,index) in list" :key="index" >
           <router-link :to='"/FIntroduce/"+index +"/"+ item.user_id'>
            <img :src="item.avatar" alt="">
            <p>{{item.username}}</p>
            </router-link>
        </div>
    </div>
   <div class="load">
     <van-loadingtype="spinner"color="#3FC4E5"v-if="isload"/>
   </div>
   <div class="msg" v-if="ismsg">
 <p></p>
 <button @click="friend"></button>
    </div>
  </div>
</template>

<script>
import qs from "qs";
import Navbar from "../public/navbar";
import { mapState } from 'vuex'
import { mapMutations } from 'vuex'
export default {
  props: {},
  data() {
    return {
       ismsg:false,
      isload:true,
      islist:false,
      msg:"",
      question:"",
      title: "",
      lflag:true,
      list:[
           {username:"",avatar:""}   ///static/imgs/touxiang.png
         
      ]
    };
  },
  watch: {
    msg(){
      this.question =""
    }
  },
  created() {
    // console.log(this.$store.state)
      this.axios.post('/entry',qs.stringify({
       user_id:this.$store.state.self.userId,
       token:this.$store.state.self.token
      })).then(res=>{
        console.log(res.data.data)
        this.list = res.data.data
     
 if(this.list.length>0){
         this.isload=false
  this.islist=true
          }else{
             this.isload=false
            this.ismsg= true
          }
      })
  },
  mounted() {
    
  },
  computed:mapState([
    // num(){
    //   return this.$store.state.totalPrice
    // }
     'totalPrice',
     'num'
  ]),
  methods: {
    // test(index){
    //  this.$router.push({path:`/FIntroduce/${index}`})
    // },
     friend(){
   this.$router.push('Searchfriend')
    },
    goMymess(){
this.$router.push('/Mymessage')
    },
      test1(){
  this.axios
        .post(
          "/blackList",
          qs.stringify({
            user_id: this.$store.state.self.userId,
            //del_user_id: this.$route.params.userId,
            token: this.$store.state.self.token
          })
        )
        .then(res => {
          console.log(res.data);
     
        });
    },
       test2(){
  this.axios
        .post(
          "/black",
          qs.stringify({
            user_id: this.$store.state.self.userId,
            //del_user_id: this.$route.params.userId,
            to_user_id:"238",
            token: this.$store.state.self.token
          })
        )
        .then(res => {
          console.log(res.data);
     
        });
    },
    ...mapMutations([
       "changeNum"
    ]),
    ...mapMutations({
   add:'changeNum'
    }),
    btn(){
    //  this.$store.commit({
    //    type:"changeNum",
    //    price:10,
    //    count:2
    //  })
    this.$store.dispatch('changeNum')
     console.log(this.$store.getters.doneTodos)
    },
      test(){
       this.$router.push("SelectFriend")
      },
      addFriend(){
          this.$router.push('/Searchfriend')
      },
      jump(){
        this.$router.push('/NewFriend')
      }
  },

  

  components: {
    Navbar
  }
};
</script>

<style scoped lang="less">
.msg{
  margin:1rem auto;
  p{
    font-size: .32rem;
    text-align: center;
  }
  button{
   
    font-size: .28rem;
    margin-left: 2.7rem;
    margin-top: .1rem;
  }
}
.load{
   margin: 3rem 3rem
}
ul{
   margin-top: 1.28rem;
    li{
        display: flex;
        margin-bottom: .24rem;
        
        img{
            width: .96rem;
            height: .96rem;
           
        }
        p{
            color: #000000;
            font-size: .36rem;
            margin-left: .25rem;
            margin-top: .31rem;
        }
    }
}
.friendlist{
    margin-top: .28rem;
    p{
        margin-left: .36rem;
        padding-top:.28rem;
        padding-bottom: .28rem;
        color: #000000;
        font-size: .36rem;
        font-weight: 400;
    }
    .list{
        
        a{
        display: flex;
        margin-bottom: .24rem;
        }
        img{
            width: .96rem;
            height: .96rem;
            border-radius: 50%;
        }
    }
}
</style>



